import {oneOf} from "../../utils/utils";

export default {
  name: 'myInputButton',
  props: {
    placeholder: {
      default: '请输入内容',
      type: String
    },
    widthClass: {
      default: 'width: 150px',
      type: String
    },
    selectWidthClass: {
      default: 'width: 100px',
      type: String
    },
    size: {
      defalult: 'medium',
      validate: function (val) {
        return oneOf(val, ['large', 'medium', 'small', 'mini']);
      }
    },
    type: {
      defalult: '',
      validate: function (val) {
        return oneOf(val, ['success', 'info', 'warning', 'danger', 'primary']);
      }
    },
    plain: {
      default: false,
      type: Boolean
    },
    iconStyle: {
      default: '',
      type: String
    },
    showSelect: {
      default: false,
      type: Boolean
    },
    clearable: {
      default: false,
      type: Boolean
    },
    options: {
      default: function () {
        return [];
      },
      type: Array
    }
  },
  computed: {

  },
  mounted() {
    this.showIcon = this.iconStyle !== '';
  },
  data() {
    return {
      inputValue: '',
      selectValue: '',
      showIcon: false
    }
  },
  methods: {
    handleClick(data) {
      let obj = {};
      if (!!this.selectValue){
        obj = {
          select: this.selectValue,
          input: this.inputValue
        }
      }else if(!this.selectValue){
        obj = {
          input: this.inputValue
        }
      }
      this.$emit('click', obj);
    },
    selectChange(data) {
      this.selectValue = data;
    }
  }
}
