import mixins from "~/utils/mixins";
import {common} from "~/utils/api/url";
import {MessageError, MessageSuccess} from "~/utils/utils";
import appTypeValidater from "~/utils/validater/appTypeValidater";

export default {
  mixins: [mixins, appTypeValidater],
  data(){
    return {
      subTitle: '',
      tableData: [],
      dialogType: false,
      visibleConfim: false,
      dialogLoading: false,
      searchKey: '',
      form: {
        id: '',
        name: '',
        enable: true
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        searchKey: this.searchKey
      };
      this.$axios.get(common.server_type_list, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    addInfo(){
      this.dialogType = true;
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    editInfo(item){
      this.form = {
        id: item.id,
        name: item.category_name,
        enable: item.enable
      };
      this.dialogType = true;
    },
    deleteInfo(item){
      this.form.id = item.id;
      this.visibleConfim = true;
    },
    statusInfo(item, status){
      let params = {
        id: item.id,
        enable: status
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.server_type_enable, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
      });
    },
    closeDialog(event){
      this.form = {
        id: '',
        name: '',
        enable: true
      };
      this.subTitle = "";
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    cancelDialog(){
      this.dialogType = false;
    },
    okDialog(){
      let url = '';
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            categoryName: this.form.name,
          };
          if (this.form.id != ''){
            params['id'] = this.form.id;
          }
          url = common.server_type_save;
          params = this.$qs.stringify(params);
          this.$axios.post(url, params).then(res => {
            if (res.data.code == 200){
              this.dialogType = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        id: this.form.id
      }
      url = common.server_type_delete;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    }
  }
}
